import mem_two from "../assets/images/mem_two.png";
import { useClipboard } from 'use-clipboard-copy';
import copy from "../assets/images/copy.svg";

const Preheader = () => {

    const clipboard = useClipboard({
        onSuccess() {
          console.log('Text was copied successfully!')
        },
        onError() {
          console.log('Failed to copy text!')
        }
      });

    return ( 
        <>
            <div className="tokenomics">
                <div className="wrapper">
                <div className="copy">
                    <h2>CA:</h2>
                    <div className="copylabel"><input type="text" ref={clipboard.target} value="trumpEJ72GKhpgKkrfLtWCf6yZT2dPY781f8sy7uPTW"></input><button className="copybutton" onClick={clipboard.copy}><img className="copyicon" src={copy} alt="" /></button></div>
                </div>
                <div className="content-cont-dog">
                            <div className="content-dog">
                                <p className="content"> 
                                    With a community-driven ethos and wild enthusiasm, <br />
                                    Trump Verse is set to revolutionize the crypto sphere. <br />
                                    Get ready to ride the wave of excitement and watch <br />
                                    your investments soar to new heights! <br />
                                    <br />
                                    The ticker is $TRUM <br />
                                    Located on Solana Blockchain <br />
                                    Based and experienced <br />
                                    team behind the project <br />
                                </p>  
                            </div>
                            <div className="content-img-dog">
                                <img src={mem_two} alt="" />
                            </div>
                        </div>
                </div>
            </div>
        </>
    );
}
 
export default Preheader;